@import url(https://fonts.googleapis.com/css2?family=Patua+One&display=swap);
*{box-sizing:border-box}html{height:100%}body{background:linear-gradient(#fdf15f, #ffbc4b);font-family:"Patua One";font-size:1.5rem;color:#371003;height:100%}.App{margin:0 auto;display:flex;flex-direction:column;flex-wrap:wrap;align-content:center}button{padding:0;border:0;background:transparent;font-size:2rem}#timer-display{margin:1rem;padding:2rem 0;border:0.2rem solid #371003;border-radius:20rem;text-align:center;font-size:2rem;box-shadow:0 2px 4px 0 rgba(0,0,0,0.1),0 4px 10px 0 rgba(0,0,0,0.1)}#timer-label{padding-bottom:0.7rem}#timer-left{padding-top:0.7rem}.timer-button{display:flex;justify-content:center}#start_stop{margin-right:0.7rem}#reset{margin-left:0.7rem}.timer-controller{display:flex;justify-content:space-between;margin:2rem}#break-label,#session-label{margin:0}.spinner{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center}

